<template>
    <div id="Info">
        <header class="info">
            <div class="head-bg">
                <img src="./../assets/img/headBg.png" alt="head-bg"/>
            </div>
            <div class="head-img"></div>
            <div class="head-name text-center">
                    <span>
                        {{$store.state.owner}}
                    </span>
                <el-tooltip class="item"
                            effect="dark"
                            content="https://github.com/xll032"
                            placement="bottom-start">
                    <a href="https://github.com/xll032" target="_blank">
                        <img src="./../assets/img/git_icon.png" alt="git">
                    </a>
                </el-tooltip>
            </div>
        </header>
        <div class="type">
            <header>
                <img src="./../assets/img/tags_icon.png"
                     alt="tags">
                <span>分类</span>
            </header>
            <el-tag v-for="(item, index) in $store.state.tags"
                    :type="item.type"
                    :key="index"
                    size="small"
                    @click.native="searchForType(item.name)">
                {{item.name + '(' + tagSum(item.name) + ')'}}
            </el-tag>
        </div>
    </div>
</template>
<script>
export default {
  methods: {
    // 统计 blog 类型总量
    tagSum (name) {
      const that = this;
      let count = 0;
      that.$store.state.initialBlogData.forEach((e, i) => {
        e.tags.forEach((element, index) => {
          if (element.name === name) {
            count++;
          }
        });
      });
      return count;
    },
    // 按类型查找 blog
    searchForType (name) {
      const that = this;
      that.$store.commit('searchForType', name);
      that.$router.push('/blog');
    }
  }
};
</script>
<style lang="scss" scoped>
    @import './../assets/css/conifg/basic.config';

    #Info {

        & {
            min-height: 600px;
        }

        .info {

            & {
                width: 100%;
                overflow: hidden;
                position: relative;
            }

            .head-bg {

                & {
                    overflow: hidden;
                    width: 100%;
                    height: 116px;
                    background: #4783aa;
                    position: absolute;
                    z-index: 0;
                }

                img {
                    width: 100%;
                }
            }

            .head-name {

                & {
                    font-size: 18px;
                    position: relative;
                }

                img {

                    & {
                        cursor: pointer;
                        margin-left: 10px;
                        opacity: 0.2;
                        position: relative;
                        top: 4px;
                        @include transition;
                    }

                    &:hover {
                        opacity: 1;
                    }

                }

            }

            .head-img {
                width: 80px;
                height: 80px;
                margin: 76px auto 8px auto;
                @include border-radius(50%);
                background:url('../assets/img/head.png') no-repeat;
                background-size:100% 100%;
                position: relative;
                z-index:1;
                -webkit-box-shadow: 0 0 8px 0 $primary-color;
                -moz-box-shadow: 0 0 8px 0 $primary-color;
                box-shadow: 0 0 8px 0 $primary-color
            }
        }

        .type {

            & {
                padding:0 4px;
                font-size: 14px;
                color: $light-gray;
            }

            header {
                margin:4px 0;
            }

            .el-tag {
                margin: 2px;
                cursor: pointer;
            }
        }
    }
</style>



